            const items = ref(props.item)
            const dialog = ref(false)
            const data = ref({})
            const handclick=()=>{
                items.value = props.item
                get_DetailThuTuc(props.item.primKey).then((data_)=>{
                  data.value = data_.data.resp
                  console.log('ay',data_.data.resp.CachThucThucHien[0].ThoiGian)
                })

            }

            // hm ny dng  ni chui ( phi khai bo nhiu)
            const join_text =(arr,path)=>{ // khng th trc tp mng vo hm m phi qua mt bc kim tra mng c tn ti (vn  bt ng b)
              if(arr.length>0)
              return  arr.map(e=>e[path]).join(", ")

            }

            

            return {
                dialog,
                items,
                handclick,
                data,
                join_text
            }

        }
        



