export const taocock = (name,token,time) => {
    // console.log(name)
    document.cookie = name+"=" + token + ";   max-age="+ time +"; path=/"; //Thuc tnh httpOnly cho bit cookie khng th c truy cp bi m JavaScript, nhm ngn chn cc cuc tn cng XSS
    //  thi gian tn ti 600 pht
    //Thuc tnh secure cho bit cookie ch c gi qua giao thc HTTPS, nhm bo v cookie khi b nh cp bi cc bn th ba
}

export function getCookie(name) {
    // Ly tt c cc cookie
    let cookies = document.cookie;
    // Tch chui thnh mng cc cp kha-gi tr
    let cookieArray = cookies.split(';');
    // Duyt qua mng  tm kha mong mun
    for (let i = 0; i < cookieArray.length; i++) {
      // Loi b khong trng tha
      let cookiePair = cookieArray[i].trim();
      // Kim tra xem kha c khp vi tn khng
      if (cookiePair.indexOf(name + '=') == 0) {
        // Tr v gi tr ca cookie
        // console.log(cookiePair.substring(name.length + 1, cookiePair.length))
        return cookiePair.substring(name.length + 1, cookiePair.length);  // ct chui b i phn name 
      }
    }
    // Nu khng tm thy kha, tr v null
    console.log('khng tm thy kha cookie no c ')
    return null;
  }


  